.header {
	background-color: $verde;

	.contenido-header {
		@include movil {
			padding: $separacion / 2 0; 
			display: grid;
			grid-template-rows: 2 1fr;
			gap: 2rem;;
		}
		@include tablet {
			padding: $separacion / 2 0; 
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}


	.logo {
		flex-basis: calc(20% - 1rem);
		}

	&.fijo {
		position: fixed;
		width: 100%;
		box-shadow: 0 .3rem .4rem #444;
	}
}

.navegacion-principal {

	display: flex;
	gap: 2rem;
	justify-content: center;
	float: right;
	a {
		color: $blanco;
		font-size: 2rem;
		&:hover {
			color: $amarillo;
		}
	}

	
}